import React, { useState } from "react";
import emailjs from "emailjs-com";
import "./contact.css";
const Contact = () => {
  const [status, setstatus] = useState("Send");

  const sendEmail = e => {
    e.preventDefault();
    setstatus("Sending");
    emailjs
      .sendForm(
        "gmail",
        "template_TwvJFDnU",
        e.target,
        "user_fH1IACk27gQFeoG2h4xlO"
      )
      .then(
        result => {
          setstatus("Sent Successfully");
        },
        error => {
          setstatus("Failed");
        }
      );
  };
  return (
    <div className="container h-100 d-flex align-items-center justify-content-center text-light">
      <form class="w-50" onSubmit={sendEmail}>
        <h3>Contact Me</h3>
        <hr class="bg-light" />
        <div class="form-group">
          <label>
            Name<span class="text-danger">*</span>
          </label>
          <input
            name="from_name"
            type="text"
            required
            class="form-control bg-transparent border-0 p-0 text-light"
            placeholder="Enter name"
          />
          <hr class="m-0 bg-light" />
        </div>
        <div class="form-group">
          <label>
            Email<span class="text-danger">*</span>
          </label>
          <input
            name="user_email"
            type="email"
            required
            class="form-control bg-transparent border-0 p-0 text-light"
            placeholder="Enter email"
          />
          <hr class="m-0 bg-light" />
          <small id="emailHelp" class="form-text text-muted">
            I'll never share your email with anyone else.
          </small>
        </div>
        <div class="form-group">
          <label>Phone</label>
          <input
            name="user_phone"
            type="text"
            class="form-control bg-transparent border-0 p-0 text-light"
            placeholder="Enter phone no (Optional)"
          />
          <hr class="m-0 bg-light" />
        </div>
        <div class="form-group">
          <label>
            Message<span class="text-danger">*</span>
          </label>
          <textarea
            name="message"
            class="form-control bg-transparent border-0 p-0 text-light"
            rows="3"
            placeholder="Your message"
          ></textarea>
          <hr class="m-0 bg-light" />
        </div>
        <button
          type="submit"
          class={`btn ${
            status === "Send"
              ? "btn-secondary"
              : status === "Sending"
              ? "btn-warning"
              : status === "Sent Successfully"
              ? "btn-success"
              : status === "Failed"
              ? "btn-danger"
              : "btn-secondary"
          }`}
        >
          {status}
        </button>
      </form>
    </div>
  );
};

export default Contact;